















































import djRecommendList from '@/components/djRecommendList.vue';
import nowPlay from '@/components/nowPlay.vue';
export default {
	data() {
		return {
			bannersList: '',
			swiper: {
				indicatorDots: true,
				autoplay: true,
				interval: 5000,
				duration: 500,
				circular: true,
				indicatorActiveColor: '#e91e63',
				previousMargin: '10px'
			},
			personalizedList: '',
			topMvList: '',
			toplistArtistList: '',
			djRecommendList: '',
			djListNumber:'5'
		}
	},
	components: {
	    djRecommendList,
		nowPlay
	},
	onLoad() {
		this.getBannerList(); //获取banner
		this.getPersonalizedList(); //获取推荐歌单
		this.getTopMvList(); //获取mv排行
		this.getToplistArtistList(); //获取歌手榜单
		this.getDjRecommendList(); //获取推荐电台
	},
	methods: {
		getBannerList() {
			
			//获取banner
			var _this = this;
			uni.showLoading({
				title: '加载中'
			});
			uni.request({
				url: _this.MusicApi.server + _this.MusicApi.type.banner,
				success: (res) => {
					uni.hideLoading();
					//console.log(res.data);
					if (res.data.code == 200) {
						_this.bannersList = res.data.banners;
					} else {
						//console.error("数据获取失败");
						uni.showToast({
							title: 'banner数据获取失败',
							duration: 5000,
							icon: 'none'
						});
					}
				},
				fail: (err) => {
					uni.hideLoading();
					uni.showToast({
						title: '请求超时',
						duration: 5000,
						icon: 'none'
					});
				}
			})
		},
		getPersonalizedList() {
			
			//获取推荐歌单
			var _this = this;
			uni.showLoading({
				title: '加载中'
			});
			uni.request({
				url: _this.MusicApi.server + _this.MusicApi.type.personalized,
				success: (res) => {
					uni.hideLoading();
					//console.log(res.data);
					if (res.data.code == 200) {
						_this.personalizedList = res.data.result;
					} else {
						console.error("数据获取失败");
						uni.showToast({
							title: 'banner数据获取失败',
							duration: 5000,
							icon: 'none'
						});
					}
				},
				fail: (err) => {
					uni.hideLoading();
					uni.showToast({
						title: '请求超时',
						duration: 5000,
						icon: 'none'
					});
				}
			})
		},
		getTopMvList() {
			
			//获取mv排行
			var _this = this;
			uni.showLoading({
				title: '加载中'
			});
			uni.request({
				url: _this.MusicApi.server + _this.MusicApi.type.topMv,
				data: {
					limit: 10
				},
				success: (res) => {
					uni.hideLoading();
					//console.log(res.data);
					if (res.data.code == 200) {
						_this.topMvList = res.data.data;
					} else {
						console.error("数据获取失败");
						uni.showToast({
							title: 'banner数据获取失败',
							duration: 5000,
							icon: 'none'
						});
					}
				},
				fail: (err) => {
					uni.hideLoading();
					uni.showToast({
						title: '请求超时',
						duration: 5000,
						icon: 'none'
					});
				}
			})
		},
		getToplistArtistList() {
			
			//获取歌手榜单
			var _this = this;
			uni.showLoading({
				title: '加载中'
			});
			uni.request({
				url: _this.MusicApi.server + _this.MusicApi.type.toplistArtist,
				data: {
					limit: 10
				},
				success: (res) => {
					uni.hideLoading();
					//console.log(res.data);
					if (res.data.code == 200) {
						_this.toplistArtistList = res.data.list.artists;
					} else {
						console.error("数据获取失败");
						uni.showToast({
							title: 'banner数据获取失败',
							duration: 5000,
							icon: 'none'
						});
					}
				},
				fail: (err) => {
					uni.hideLoading();
					uni.showToast({
						title: '请求超时',
						duration: 5000,
						icon: 'none'
					});
				}
			})
		},
		getDjRecommendList() {
			
			//获取推荐电台
			var _this = this;
			uni.showLoading({
				title: '加载中'
			});
			uni.request({
				url: _this.MusicApi.server + _this.MusicApi.type.djRecommend,
				data: {
					limit: 10
				},
				success: (res) => {
					uni.hideLoading();
					//console.log(res.data);
					if (res.data.code == 200) {
						_this.djRecommendList = res.data.djRadios;
					} else {
						console.error("数据获取失败");
						uni.showToast({
							title: 'banner数据获取失败',
							duration: 5000,
							icon: 'none'
						});
					}
				},
				fail: (err) => {
					uni.hideLoading();
					uni.showToast({
						title: '请求超时',
						duration: 5000,
						icon: 'none'
					});
				}
			})
		},
		openMvDetail(mvid){
			
			//跳转mv详情
			var mvid = mvid;
			uni.navigateTo({
				url: '../mvDetail/mvDetail?mvid='+mvid
			});
		},
		openDjList(){
			//跳转mv详情
			uni.navigateTo({
				url: '../djList/djList'
			});
		},
		openPersonalizedDetail(id){
			
			//跳转歌单详情
			var id = id;
			uni.navigateTo({
				url: '../getPlaylistDetail/getPlaylistDetail?id='+id
			});
		},
		openPersonalizedList(){
			
			//跳转推荐歌单列表
			uni.navigateTo({
				url: '../personalizedList/personalizedList'
			});
		},
		openMvList(){
			
			//跳转推荐歌单列表
			uni.navigateTo({
				url: '../mvList/mvList'
			});
		}
	}
}
